import { Form, Field, Formik } from 'formik';
import { useAuth } from 'hooks/useAuth';
import { useDispatch } from 'react-redux';
import { identifyMe } from '../../redux/operations';

export const IdentForm = () => {
  const dispatch = useDispatch();
  const { user } = useAuth();
  return (
    <Formik
      initialValues={{
        type: '',
        company: '',
      }}
      onSubmit={(values, action) => {
        console.log(values);
        const data = {
          user: user,
          type: values.type,
          company: values.company,
        };
        console.log(data);
        dispatch(identifyMe(data));
      }}
    >
      <Form>
        <label>
              
          <Field as="select" name="company">
            <option value=""></option>
            <option value="Legko"></option>
            <option value="MPF"></option>
            <option value="VPF"></option>
          </Field>
        </label>
        <label>
            
          <Field as="select" name="type">
            <option value=""></option>
            <option value="inspektor"></option>
            <option value="master"></option>
            <option value="komirnik"></option>
          </Field>
        </label>
        <button type="submit">OK</button>
      </Form>
    </Formik>
  );
};
